<template>
    <div>
        <h1>Summary</h1>
        <div>
            <p style="display: flex ;gap:8px; justify-content: right">
                <span>TotalDeal:</span>
                <span>{{ total }}</span>
                <span>Win:</span>
                <span>{{ numberWin }}</span>
                <span>Lose:</span>
                <span>{{ numberLose }}</span>
            </p>
        </div>
        <table class="table table-striped">
            <thead>
            <tr>
                <th>Code</th>
                <th>Buy Date</th>
                <th>Current</th>
                <th>Buy Price</th>
                <th>Target</th>
                <th>Percentage (%)</th>
                <th>Wave</th>
                <th>Crossing Ema</th>
                <th>Candle</th>
                <th>Rsi Stage</th>
                <th>Trend</th>
                <th>Scores </th>
                <th>Sale Date</th>
                <th>Profit (%)</th>
            </tr>
            </thead>
            <tbody>
            <tr :key="item[index]" v-for="(item,index) in data">
                <td>
                    <a :href="item.url" target="_blank">{{item.code}}</a>
                </td>
                <td>{{item.time}}</td>
                <td>{{item.current}}</td>
                <td>{{item.close}}</td>
                <td>{{item.target}}</td>
                <td :style="item.style">{{ calPercent(item.current,item.close) }}</td>
                <td>{{item.wave}}</td>
                <td>{{item.is_crossing}}</td>
                <td>{{item.candle}}</td>
                <td>{{item.rsi_stage}}</td>
                <td>{{item.s_trend}}</td>
                <td>{{item.scores}}</td>
                <td>{{item.sale_time}}</td>
                <td>{{item.profit}}</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import axios from 'axios'
import {
    GOOGLE_DRIVE_URL,
    GOOGLE_DRIVE_API_KEY,
    GOOGLE_DRIVE_SUMMARY_FILE_STOCK_ID,
    GOOGLE_DRIVE_SUMMARY_FILE_COIN_ID,
} from '../common/config'

export default {
    name: 'SummaryIndex',
    header: {
        title: 'Summary Index',
        subTitle: 'Summary',
    },

    watch: {
        serviceDataCoin: function (newVal) {
            newVal.forEach(item => {
                const current =  parseFloat(item.data.k.c)
                this.data[item.originalIndex].current = current
                if(current > this.data[item.originalIndex].close){
                    this.data[item.originalIndex].style = {color: 'green'}
                }else if(current < this.data[item.originalIndex].close){
                    this.data[item.originalIndex].style = {color: 'red'}
                }else {
                    this.data[item.originalIndex].style = {color: 'yellow'}
                }
            })
        },
        serviceDataStock: function (newVal) {
            newVal.forEach(item => {
                const current =  parseFloat(item.data.Close)
                this.data[item.originalIndex].current = current
                if(current > this.data[item.originalIndex].close){
                    this.data[item.originalIndex].style = {color: 'green'}
                }else if(current < this.data[item.originalIndex].close){
                    this.data[item.originalIndex].style = {color: 'red'}
                }else {
                    this.data[item.originalIndex].style = {color: 'yellow'}
                }
            })
        }
    },

    data() {
        return {
            data: [],
            type : this.$route.query.t,
            fileId : GOOGLE_DRIVE_SUMMARY_FILE_STOCK_ID,
            googleDriveUrl :GOOGLE_DRIVE_URL,
            googleDriveApiKey :GOOGLE_DRIVE_API_KEY,

            serviceDataCoin: [],
            serviceDataStock: [],
            numberWin: 0,
            numberLose: 0,
            total: 0,
        }
    },
    created() {
        this.fileId = this.type === 'stock' ? GOOGLE_DRIVE_SUMMARY_FILE_STOCK_ID : GOOGLE_DRIVE_SUMMARY_FILE_COIN_ID;
        let url = this.googleDriveUrl + this.fileId + '?key=' + this.googleDriveApiKey + '&alt=media'
        axios.get(url).then(response => {
            this.setCurrentPrice(response.data)
            this.getServiceData()
        }).catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
        })
    },
    methods: {
        setCurrentPrice(data) {
            for(let item in data) {
                this.data.push({
                    current: 0,
                    code: data[item].code,
                    close: data[item].close,
                    target:  parseFloat(data[item].target.toFixed(this.countDecimals(data[item].close))),
                    wave: data[item].wave,
                    is_crossing: data[item].is_crossing,
                    candle: data[item].candle,
                    s_trend: data[item].s_trend,
                    scores: data[item].scores,
                    time: data[item].time,
                    profit:  parseFloat(data[item].profit.toFixed(2)),
                    url: this.getUrl(data[item]),
                    sale_time: data[item].sale_time,
                    style: {color: 'yellow'},
                })
            }
        },

        calPercent(current, close) {
            return ((current - close) / close * 100).toFixed(2)
        },

        getUrl(item) {
            const serviceUrl = 'https://tradingview.com/chart'
            return serviceUrl+'/?symbol='+item['exchange']+':'+item['code']
        },

        countDecimals(value) {
            if (Math.floor(value) === value) {
                return 0
            }
            return value.toString().split('.')[1].length || 0
        },

        getServiceData() {
            const data = Object.keys(this.data).map(key => this.data[key])
            this.numberWin = data.filter(item => item.profit > 0).length
            this.numberLose = data.filter(item => item.profit < 0).length
            this.total = data.length
            if(this.type === 'coin') {
                this.getServiceDataForCoin()
            }else{
                const now = new Date()
                // eslint-disable-next-line no-console
                console.log(now)
                const hour = now.getHours()
                if(hour >= 9 && hour <= 15) {
                    this.getServiceDataForStock()
                }
            }
        },

        getServiceDataForCoin() {
            for(let originalIndex in this.data) {
                let symbol = this.data[originalIndex]['code']
                let ws = new WebSocket('wss://stream.binance.com:9443/ws/'+symbol.toLowerCase()+'@kline_1h')
                ws.onmessage = (event) => {
                    let data = JSON.parse(event.data)
                    this.serviceDataCoin.push({
                        symbol: symbol,
                        originalIndex: originalIndex,
                        data: data,
                    })
                }
            }
        },

        getServiceDataForStock(codes = []) {
            //get all code unique from data
            if(codes.length === 0) {
                this.data.forEach(item => {
                    if(codes.indexOf(item.code) === -1) {
                        codes.push(item.code)
                    }
                })
            }

            let stringCodes = codes.join(',')

            axios.get('https://cryptic-ridge-06951.herokuapp.com/call?codes='+stringCodes).then(response => {
                let data = response.data
                if(data.status === 'error'){
                    return
                }
                if(data.length > 0) {
                    for (let originalIndex in this.data) {
                        let code = this.data[originalIndex]['code']
                        let dataItem = data.find(item => item.code === code)
                        this.serviceDataStock.push({
                            symbol: code,
                            originalIndex: originalIndex,
                            data: dataItem.price[0],
                        })
                    }
                    this.getServiceDataForStock(codes)
                }
            }).catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
            })
        }
    },
}
</script>
